import MyCarousel from "./components/carousel/MyCarousel";
import "./App.css";

function App() {

const rightClickHandler = (e) => {
  if(e.button == 2) { 
    return false;
  }
}


  return (
    <div className="App">
      <MyCarousel onClick={rightClickHandler}/>
    </div>
  );
}

export default App;
