import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import "./MyCarousel.css"
import React from 'react'


let MyPicArr = [];

for (let i = 1; i <= 9; i++) {

    let myPicureSrc = `/images/${i}.jpg`
    MyPicArr.push(myPicureSrc)
}

console.log(MyPicArr);


const MyCarousel = ({ onClick }) => {

    const settings = {
        dots: false,
        infinite: true,
        speed: 700,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: true,
        autoplay: true,
        autoplaySpeed: 4000,
        centerPadding: "20px",
        pauseOnHover: true,
        centerMode: true,
        //fade:true,
    };

    return (
        <div id="my-carousel">
            <Slider {...settings}>
                {MyPicArr.map((pic, id) => {
                    return (
                        <div className="slide-container" >
                            <img width="100%" onClick={onClick} src={pic} alt="img" />
                        </div>
                    )
                })}
            </Slider>
        </div>
    )
}

export default MyCarousel
